.developer{
    background: rgb(255, 255, 255);
    width: 100%;
    height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.img-d {
    width: 15rem;
    border-radius: 20px;
    border: 3px solid rgb(56, 121, 133);
    box-shadow: 3px 2px #ded5d5;
}

.text-p{
    width:20%;
    position:relative;
    right: 3rem;

}

.text {
    width: 35rem;
    color:  rgb(193, 195, 197);;
    padding: 0.5rem;
}

.d-title {
    margin-top: 1rem;
    font-weight: bold;
    color:  #042c3f;
    font-size: 1.5rem;
}

.foto-d{
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width:1000px) {

    .developer{
        height: 20vh;
    }

    .img-d {
        width: 8rem;
        border-radius: 20px;
        border: 3px solid rgb(56, 121, 133);
        box-shadow: 3px 2px #ded5d5;
    }

    .d-title {
        font-size: 16px;
    }
    
    .text-p{
        width: 72%;
        position: relative;
        left: 0;
        font-size: 8px
    }
}