.intro {
    display: flex;
    width: 100%;
    height: 100vh;
    background-color: #042c3f;
    justify-content: center;
    align-items: center;
    overflow: hidden;

}

.i-left{
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 2rem;
}

.i-name{
    display: flex;
    flex-direction: column;
    width: 50vw;
    margin-left: 1.8rem;
}

.whap-t{
    background: #042c3f;
    border: 0;
    cursor: pointer;
}

.i-name>:nth-child(1){
    color: #c1ebf5;
    font-weight: bold;
    font-size: 4rem;
}

.i-name>:nth-child(2){
    color: rgb(172, 79, 79);
    font-weight: bold;
    font-size: 3rem;
}

.i-name>:nth-child(3){
    font-weight:100;
    font-size: 20px;
    color: var(--font-color-1);
    margin-top: 10px;
    font-size: 1rem;
}

.i-icon{
    margin-top: 12px;
    display: flex;
    column-gap: 1rem;
    position: relative;
    left: 2rem;
}

.i-right{
    flex: 1;
    position: relative;
    margin-left: 5rem;


}

.img-right {
    width: 30rem;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}

@media screen and (max-width: 700px){
    .intro{
        height: 100vh;
    }
}

@media screen and (max-width:1000px){
    .intro {
        height: 38vh;
    }
    .img-right {
        width: 12rem;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        position: relative;
        right: 12px;

    }

    .i-name{
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-left: 1.8rem;
    }
    
    .i-name>:nth-child(1){
        color: #c1ebf5;
        font-weight: bold;
        font-size: 2rem;
    }
    
    .i-name>:nth-child(2){
        font-weight: bold;
        font-size: 2rem;
    }
    
    .i-name>:nth-child(3){
        font-weight:100;
        font-size: 20px;
        margin-top: 10px;
        color: var(--font-color-1);
        font-size: 0.5rem;
    }

    .i-icon{
        margin-top: 12px;
        display: flex;
        column-gap: 1rem;
        position: relative;
        left: -0.5rem;
    }

}

@media screen and (max-width:400px){
    .i-right{
        left: -18px;
    }
}