.navbar {
    width: 100%;    
    height: 60px;
    color: var(--font-color-1);
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    background:  #042c3f;
}

.img-perfil {
    width: 3rem;
    height: 3rem;
    border-radius: 12px;
    border: 3px solid rgb(56, 121, 133);
    margin: 0;
}

.n-right-list {
    display: flex;

    margin-right: 0;
    cursor: pointer;
    list-style: none;
    position: absolute;
    right: 0;
    top: 0;
}

li{
    margin: 1rem;
    
}

li:hover {
    color: rgb(251, 251, 251);
}

.n-left{
    flex: 1;
    align-items: center;
    display: flex;
    justify-content: center;
    left: 1.5rem;
    position: absolute;
    cursor: pointer;
}

.n-left:hover {
    background-color: #0a5c82; 
    border-radius: 20px;
}

a{
    text-decoration: none;
}

.n-left-name{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0;
    padding: 8px;
    text-decoration-line: none;
    color: var(--font-color-1);
}

.text-right {
    font-size: 10px;
    margin: 0;
    color: rgb(193, 195, 197);
}

@media screen and (max-width:1000px){

    .navbar{
        font-size: 8px;
    }

    .n-right-list {
        display: flex;
        position: absolute;
        column-gap: 1rem;
        margin-right: 0;
        right: 12px;
        top: 21px;
    }

.n-right{
    margin: 0;
    padding: 0;
}

    li{
        margin: 0;
        white-space: nowrap;
    }

    .n-left-name{
        white-space: nowrap;
    }

    .text-right{
        font-size: 8px;
    }

    .img-perfil{
        width: 2rem;
        height: 2rem;
    }


}

@media screen and (max-width:400px){
    .n-left{
        left: 10px;
    }
}