.references{
    width: 100%;
    height: auto;
    background: #042c3f;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.coment{
    width: 40rem;
    margin: 1.5rem;

}

.t-references{
    width: 40%;
}

.t-references>:nth-child(1){
    color: #c1ebf5;
    font-weight: bold;
    font-size: 4rem;
}

.t-references>:nth-child(2){
    color: #c1ebf5;
    font-weight: 100;
    font-size: 3rem;
    display: flex;
    flex-direction: column;
}

.t-references>:nth-child(3){
    font-weight:bold;
    font-size: 20px;
    margin-top: 10px;
    color: #c1ebf5;
    font-size: 2rem;
}

@media screen and (max-width:1000px){
    .references{
        flex-direction: column;
        justify-content: center;
        height: 80vh;
    }
    .coment{
        width: 20rem;
        height: 1rem;
        margin: 0;
        position: relative;
        bottom: 16rem;
    }

    .t-references{
        width: 100%;
        height: 10rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        position: relative;
        bottom: 16rem;
    }

    .t-references>:nth-child(1){
        font-size: 32px;
    }
    
    .t-references>:nth-child(2){
        font-size: 24px;
    }
    
    .t-references>:nth-child(3){
        font-size: 28px;
    }

}

@media screen and (max-width:600px){

    .references{
        height: 68vh;
    }
    .t-references>:nth-child(1){
        font-size: 24px;
    }
    
    .t-references>:nth-child(2){
        font-size: 18px;
    }
    
    .t-references>:nth-child(3){
        font-size: 22px;
    }

    .t-references{
        height: 3rem;
        bottom: 12rem;
    }

    .coment{
        position: relative;
        bottom: 8rem;
    }
}

@media screen and (max-width:400px){
    .references{
        height: 100vh;
    }
}