.card{
    width: 8rem;
    height: 3rem;
    background: white;
    border-radius: 20px;
    display: flex;
    align-items: center;
    margin: 10px;
    box-shadow: 3px 2px #ded5d5;

}

.l-img{
    width: 2rem;
    margin-left: 12px;
    border-radius: 10px;
}

.cardtext{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-left: 5px;
}

.cardtext>:nth-child(1){
    font-size: 10px;
}

.cardtext>:nth-child(2){
    font-size: 8px;
}

.f-1{
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
}

.all-card{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}

