.languaje {
    width: 50%;
    height: 70vh;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.fontend {
    width: 50%;
    height: 45vh;
    background: rgb(190, 241, 250);
    border-radius: 20px;
    padding: 10px;
}

.f-titulo{
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color:  #042c3f;
    font-size: 1.5rem;
}

@media screen and (max-width:1000px) {
    .fontend {
        width: 10rem;
        height: 30rem;
    }

    .f-titulo{
        font-size: 18px;
    }

    .all-card{
        flex-direction: column;
        position: relative;
        top: 16px;
    }
}

@media screen and (max-width:600px){
    .f-titulo{
        font-size: 14px;
    }
    .fontend{
        height: 58vh;
    }
}

@media screen and (max-width:400px){
    .languaje{
        height: 70vh;
    }
    .fontend{
        height: 55vh;
        padding: 4px;
        position: relative;
        top: 30px;
    }

    .all-card{
        top: -1px;
    }
}

