.contact{
    width: 100%;
    height: 10vh;
    background: rgb(255, 255, 255);
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width:1000px) {
    .contact{
        height: 5vh;
    }
}

@media screen and (max-width:400px){
    .contact{
        height: 7vh;
    }
}