.cole {
    width: 30rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 10px;
}

.cole-img {
    width: 18rem;
    height: 8rem;
    position: relative;
    left: -2rem;
    
}

@media screen and (max-width:1000px) {
    .cole{
        width: 10rem;
        margin-bottom: 64px;
        font-size: 8px;
    }
    .cole-img {
        width: 10rem;
        height: 6rem;
        left: -1rem;
        position: relative;
    }
}