.experience {
    width: 100%;
    height: 70vh;
    background-image: url(../../img/white.jpg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    display: flex;
    justify-content:space-around;
    align-items: center;
}

.pro-1{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.e-port-1{
    width: 25rem;
    display: flex;
}

.img-port-1{
    width: 18rem;
    height: 16rem;
    margin: 12px;
    border-radius: 20px;
    position: relative;
    top: 40px;
}

.btn-project{
    width: 5rem;
    height: 2rem;
    position: relative;
    top: 1.5rem;
    left: 20px;
    box-shadow: 3px 2px #ded5d5;
    background: #0f86fc;
    border-radius: 20px;
    color: white;
    font-weight: bold;
    border: none;
    cursor: pointer;
}

.btn-project:hover {
    background: white;
    color: #0f86fc;
}


.e-info{
    width: 21rem;
    display: flex;
    flex-direction: column;

}

.e-info>:nth-child(1){
    color: #042c3f;
    font-weight: bold;
    font-size: 4rem;
}

.e-info>:nth-child(2){
    color: #042c3f;
    font-weight: 100;
    font-size: 2rem;
    display: flex;
    flex-direction: column;
}

.e-info>:nth-child(3){
    font-weight:bold;
    font-size: 20px;
    margin-top: 10px;
    color: #042c3f;
    font-size: 1rem;
}

@media screen and (max-width:1000px){
    .experience{
        flex-direction: column;
        height: 58vh;
    }
    .e-info{
        width: 21rem;
        display: flex;
        flex-direction: column;
    
    }
    
    .e-info>:nth-child(1){
        color: #042c3f;
        font-weight: bold;
        font-size: 22px;
    }
    
    .e-info>:nth-child(2){
        color: #042c3f;
        font-weight: 100;
        font-size: 18px;
        display: flex;
        flex-direction: column;
    }
    
    .e-info>:nth-child(3){
        font-size: 12px;
        margin-top: 10px;
        color: #042c3f;
    }

    .pro-1{
        display: flex;
        justify-content:center;
        align-items: center;
    }

    .img-port-1{
        width: 14rem;
        height: 12rem;
        margin: 10px;
        border-radius: 20px;
        position: relative;
        top: 25px;
    }

    .btn-project{
        right: 0;
        left: 0;
    }
    
}

@media screen and (max-width:600px){
    .experience{
        height: 45vh;
    }

    .e-port-1{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .img-port-1{
        width: 6rem;
        height: 8rem;
        margin: 6px;
        margin-bottom: 0;
        border-radius: 20px;
        position: relative;
        top: 8px;
    }

    .btn-project{
        position: relative;
        right: 0;
        left: 0;
        top: 8px;
    }
}

@media screen and (max-width:400px){
    .experience{
        height: 50vh;
    }
}

